import React from "react";

function Overview(props) {
  return (
    <div className="overview bg-white rounded p-5" id={props.id}>
      <div className="bg-light p-5 shadow border">
        <div className="text-center  text-capitalize m-0">
          <h1 className="text-primary">Noorani Qaida</h1>
          <h3 className="m-0 text-success"> The Foundation of Arabic</h3>
        </div>
        <h4>About The Course</h4>
        <h5>naurani quida</h5>
        <div>
          <p>
            Students are taught to read the Quran using the book Al Qaida Al
            Noorani, which is written for those who are just beginning to study
            Quranic Arabic. Using verses from the Quran, the book explains how
            to pronounce the Arabic alphabets properly. The Arabic alphabets are
            used at the outset to demonstrate how they are related. The
            foundational text for learning to read the Quran is Qaida. This book
            teaches readers of all ages how to accurately interpret the Quran.
            Typically, this book is used by people in non-Arab nations. Without
            this very helpful book, we would not be aware of the recital. There
            are a few Books for learning Arabic and Quran Perusing (Noorani
            Qaida) , one among them that includes this book is best, clear and
            suitable for beginners
          </p>
        </div>
      </div>
    </div>
  );
}

export default Overview;
